//Mobile menu

const navTrigger = document.querySelector('.nav-trigger');
const trigger = document.querySelector('.nav-trigger__link');
const navMobile = document.querySelector('.nav-mobile');

trigger.addEventListener('click', e => {
  e.preventDefault();
  navTrigger.classList.toggle('is-active');
  navMobile.classList.toggle('is-active');
})

//Accordion team

const workers = document.querySelectorAll('.worker');
const teamAccord = document.querySelector('.team__accordion');

teamAccord.addEventListener('click', e => {
  e.preventDefault();

  const target = e.target;
  const item = target.parentNode;
  const content = target.nextElementSibling;
  const contentHeight = content.firstElementChild.clientHeight;

  if (target.classList.contains('worker__name-link')) {   

    for (let i of workers) {
      if (i != item) {
        i.classList.remove('is-active');
        i.lastElementChild.style.height = 0;
      }
    }
 
    if (!item.classList.contains('is-active')) {
      item.classList.add('is-active');
      content.style.height = contentHeight + 'px';
    } else {
      item.classList.remove('is-active');
      content.style.height = 0;
    }
  }
})

//Accordion menu

myAccordionMenu();

function myAccordionMenu() {
  const menuItems = document.querySelectorAll('.menu__item');
  const menuAccord = document.querySelector('.menu__accordion');

  menuAccord.addEventListener('click', e => { 
    e.preventDefault();
    workMenu(e);
  });
  
  function workMenu(e) {
    let target = e.target.parentNode;
    let content = target.nextElementSibling;
    let item = target.parentNode;
    let tarWidth = target.clientWidth;
    const curWidth = document.documentElement.clientWidth;

    if (e.target.classList.contains('menu__title')) {
      moveMenu();
    }

    target = e.target;
    content = target.nextElementSibling;
    item = target.parentNode;
    tarWidth = target.clientWidth;

    if (target.classList.contains('menu__link')) {   
      moveMenu();    
    }

    function moveMenu() {
      for (let i of menuItems) {
        if (i != item) {
          i.classList.remove('is-active');
          i.lastElementChild.style.width = 0;
        }
      }

      if (!item.classList.contains('is-active')) {
        
        item.classList.add('is-active');

        let num;

        for (let i = 0; i < menuItems.length; i++) {
          if (menuItems[i] == item) {
            num = (menuItems.length - 1) - i; 
          }
        }

        if (curWidth > 480 && curWidth < 720) {
          content.style.width = curWidth - tarWidth * menuItems.length + 'px';     
        } else if (curWidth <= 480) {
          e.currentTarget.style.transform = `translateX(${tarWidth * num}px)`;
          content.style.width = curWidth - tarWidth + 'px';
          
        } else {
          content.style.width = 520 + 'px';
        }
      } else {
        item.classList.remove('is-active');
        content.style.width = 0;
        e.currentTarget.style.transform = `translateX(0)`;
      }
    }
  }
}

//Popup reviews

const reviews = document.querySelector('.reviews__list');

reviews.addEventListener('click', function (e) {  

  if (e.target.classList.contains('btn--review')) {

    const template = document.querySelector('#overlay').content.querySelector('.review__open');
    const head = this.querySelector('.review__head').textContent;    
    const text = this.querySelector('.review__text').textContent;
    const review = template.cloneNode(true);
    const reviewClose = review.querySelector('#review-close');
    
    review.querySelector('.review__head-hide').textContent = head;
    review.querySelector('.review__text-hide > p').textContent = text;

    document.body.appendChild(review);    
    
    reviewClose.addEventListener('click', e => {
      e.preventDefault();
      
      document.body.removeChild(review);
    })
  }
})

//Popup ingredient

showIngred();

function showIngred() {
  const ingredBadge = document.querySelectorAll('.burger__badge--ingred');
  const burgerDrop = document.querySelectorAll('.burger__dropdown');
  const ingredClose = document.querySelectorAll('.modal__close--ingred');

  for (const i of ingredBadge) {
    i.addEventListener('mouseenter', remHidden);
    i.addEventListener('mouseleave', addHidden)
  }

  for (const i of ingredClose) {
    i.addEventListener('touchstart', addHidden);
  }

  function addHidden() {
    for (const i of burgerDrop) {
      i.classList.add('visually-hidden');
    }
  }

  function remHidden() {
    for (const i of burgerDrop) {
      i.classList.remove('visually-hidden');
    }
  }
}

//Slider burgers

mySlider();

function mySlider() {
  
  const next = document.querySelector('.scroll-btn--next');
  const prev = document.querySelector('.scroll-btn--prev');
  const list = document.querySelector('.slider__list');
  const items = list.querySelectorAll('.slider__item');

  next.addEventListener('click', moveNext);
  prev.addEventListener('click', movePrev);

  let num = 2;
  
  function moveNext() {

    num++;
    if (num > items.length) num = 1;

    setOrder();
    list.classList.remove('is-reverse');
    moveItem();
  }

  function movePrev() {

    num--;
    if (num === 0) num = items.length;
    
    setOrder();
    list.classList.add('is-reverse');
    moveItem();    
  }

  function setOrder() {
    let key = num;

    for (let i of items) {
      i.style.order = key;
      key++;
      if (key > items.length) key = 1;
    }
  }

  function moveItem() {
    list.classList.remove('is-move');

    setTimeout(() => {
      list.classList.add('is-move');
    }, 50);    
  }
}
